* {
  font-family: sans-serif;
  text-align: center;
}

html,
body,
div,
span,
applet,
object,
h1,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

.all {
  background-image: url("https://drive.google.com/uc?export=view&id=1wfPHsG-xqMfhL-foTbusAorFy291FTN7");
  background-size: cover;
}

.back {
  background: rgba(255, 255, 255, 0.3);
}

.slider {
  padding-left: 40%;
  padding-top: 40px;
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 24px;
  margin-bottom: 0;
  padding-bottom: 0;
}

.shousai {
  font-size: 18px;
}

@media (max-width: 1040px) {
  .all {
    background-image: url("https://drive.google.com/uc?export=view&id=1wfPHsG-xqMfhL-foTbusAorFy291FTN7");
    background-size: contain;
  }

  .back {
    background: rgba(255, 255, 255, 0.3);
  }

  .slider {
    padding-left: 30%;
    padding-top: 40px;
  }

  h1 {
    font-size: 38px;
  }

  h2 {
    font-size: 24px;
  }

  h3 {
    font-size: 20px;
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .shousai {
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .all {
    background-image: url("https://drive.google.com/uc?export=view&id=1wfPHsG-xqMfhL-foTbusAorFy291FTN7");
    background-size: contain;
  }

  .back {
    background: rgba(255, 255, 255, 0.3);
  }

  .slider {
    padding-left: 10%;
    padding-top: 40px;
  }

  h1 {
    font-size: 32px;
  }

  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 18px;
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .shousai {
    font-size: 12px;
  }
}
